body {
  width: 100%;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;
}

html, body,
h1, h2, h3, h4, h5, h6,
div, p {
  margin: 0;
  padding: 0;
}
.align-center{
  text-align: center;
}

/*オープニングアニメーション*/
.disp1{
  width: 100%;
  height: 100%;
  background-color: #202020;
  color: #fefefe;
  text-align: center;
}

/*ヘッダー・フッター*/
#header{
  transition: opacity 0.5s, visibility 0.5s;
  opacity: 0;
  visibility: hidden;
  position: fixed;
  width: 100%;
  height: 64px;
  background-color: rgba(32, 32, 32, 0.8);
  z-index: 10;
}
#header.is-show {
  opacity: 1;
  visibility: visible;
}
#header img{
  padding: 2px;
  display: inline-block;
}
.btns-header{
  display: inline-block;
  width: 80%;
  margin-top: 0px;
  top: 0px;
  height: 64px;
  overflow-x: scroll;
  white-space: nowrap;
}
.btn-header{
  line-height: 64px;
  margin: 0 16px;
  display: inline-block;
}
.btn-header-txt{
  font-size: 16px;
  color: #fefefe;
  text-decoration: none;
}
#footer{
  padding: 32px 0;
  font-size: 0.8em;
  background-color: #fefefe;
  color: #202020;
}

/*全画面動画*/
#top{
  position: relative;
  width: 100%;
  height: 100vh;
  z-index: 1;
}
/*全画面動画上のタイトル*/
#top div{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateY(-50%) translateX(-50%);
  -webkit-transform: translateY(-50%) translateX(-50%);
  width: 100%;
}
#top div p {
  color: #fefefe;
}
#bg-video{
  height: 100%;
  width: 100%;
  object-fit: cover;
  overflow: hidden;
}

/*メインコンテンツの大枠*/
.main-contents{
  padding: 64px 0;
}

/*横幅の余白のためのwrapper*/
.contents-wrapper{
  width: 80%;
  margin: auto;
  padding: 16px 0;
}

/*メインコンテンツの中の一つ一つの要素*/
.element{
  margin: 32px 0;
}

/*メインの各コンテンツ*/
#genres{
  background-color: #202020;
  color: #fefefe;
}
#infomation{
  background-color: #fefefe;
  color: #202020;
}
#contacts{
  background-color: #202020;
  color: #fefefe;
}

.info-box{
  max-height: 50vh;
  overflow-y: auto;
}

.news-date{
  font-size: 0.8em;
  color: #979797;
  text-align: right;
}

.ev-link{
  font-size: 0.8em;
}
.ev-link:before{
 content: '　';
}
.ev-link-txt{
  color: #979797;
}

.fa-external-link-alt{
  padding-left: 4px;
}

.contact{
  position: relative;
  z-index: 1;
  height: 120px;
}

.contact p{
  text-align: right;
}

.mail:before, .blog:before{
  font-family: "Font Awesome 5 Free";
}
.youtube:before, .twitter:before, .instagram:before{
  font-family: "Font Awesome 5 Brands";
}
.contact:before{
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  font-size: 100px;
  opacity: 0.8;
  font-weight: 400;
}
.mail:before{
  color: #00b900;
  content: "\f0e0";
}
.youtube:before{
  color: #cd201f;
  content: "\f167";
}
.blog:before{
  font-weight: 900;
  color: #21759b;
  content: "\f781";
}
.twitter:before{
  color: #1DA1F2;
  content: "\f099";
}
.instagram:before{
  color: #D93177;
  content: "\f16d";
}

.onepoint-color{
  color: #16E7CF;
}

@media screen and (min-width: 769px) {
  .btn-header{
    padding-left: 64px;
    display: inline-block;
  }

  #top h1{
    font-size: 4em;
  }
  #top div p {
    font-size: 1.5em;
  }

  .element{
    display: inline-block;
    width: 40%;
    margin: 5%;
    vertical-align: top;
  }
}
